body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --mainColor: #f9004d;
  --blackColor: #101010;
  --blackColor2: #191919;
  --grayColor: hsla(0,0%,100%,.6);
  --skyColor: #4db5ff;
  --whiteColor: white;
  /* var(--..ColorName..) */
}

*{
  padding: 0;
	margin: 0;
	font-family: 'Josefin Sans', sans-serif;
	box-sizing: border-box;
	text-decoration: none;
}

.app{
  background-color: #101010;
  background-color: var(--blackColor);
}

.color{
  color: #f9004d;
  color: var(--mainColor);
}

a{
  color: white;
  color: var(--whiteColor);
}
:root{
    --mainColor: #f9004d;
    --blackColor: #101010;
    --blackColor2: #191919;
    --grayColor: hsla(0,0%,100%,.6);
    --skyColor: #4db5ff;
    --whiteColor: white;
    /* var(--..ColorName..) */
}

.AboutMe{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
	width: 100%;
    background-color: #191919;
    background-color: var(--blackColor2);
    display: flex;
    flex-direction: row;
    padding: 7.5vh 0 7.5vh 0%;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.AboutImage{
width: 350px;
height: 85vh;
border-radius: 30px;
background-color: #101010;
background-color: var(--blackColor);
margin-bottom: 30px;
}

.AboutImage>.image{
    width: 350px;
    height: 85vh;
    border-radius: 30px;
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg);
    transition: 0.7s;
}

.AboutImage>.image:hover{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}

.AboutData{
    width: 600px;
    height: 85vh;
    padding: 0 5%;
    display: flex;
    flex-direction: column;
}

.AboutTitle{
    color: white;
    color: var(--whiteColor);
    text-align: center;
    font-size: 75px;
    text-transform: capitalize;
    font-family: 'Times New Roman', Times, serif;
}

.SubTitle{
    color: hsla(0,0%,100%,.6);
    color: var(--grayColor);
    padding: 5% 0;
    font-family: 'Times New Roman', Times, serif;
    font-size: 30px;
}

.description{
    padding: 0 0 5% 0;
    letter-spacing: 1px;
    line-height: 30px;
    font-size: 20px;
    color: white;
    color: var(--whiteColor);
    font-family: "labrada";
}

.Reach{
    padding: 10px 25px;
    background-color: #f9004d;
    background-color: var(--mainColor);
    color: white;
    color: var(--whiteColor);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-radius: 30px;
}

